<template>
	<v-content>
		<v-container fluid class="pa-0">
			<v-btn @click="prev()" class="prevIcon" icon dark>
				<v-icon size="48px" color="red">mdi-chevron-left</v-icon>
			</v-btn>
			<v-btn @click="next()" class="nextIcon" icon dark>
				<v-icon size="48px" color="red">mdi-chevron-right</v-icon>
			</v-btn>
			<v-row no-gutters>
				<v-col cols="6" class="pa-5">
					<h3 style="text-align:center">{{ slides[index].title }}</h3>
					<p>{{ slides[index].text }}</p>
				</v-col>
				<v-col cols="6">
					<div id="storyMap-container">
						<StoryMap :mapData="storyData" />
					</div>
				</v-col>
			</v-row>
		</v-container>
	</v-content>
</template>

<script>
	import { mapState, mapMutations, mapActions } from "vuex";
	import StoryMap from "../core_components/Maps/StoryMap";

	export default {
		name: "Story",
		components: {
			StoryMap
		},
		props: [],
		data: () => ({
			index: 0,
			slides: [
				{
					title: "All Product 3 purchased with Visa",
					text:
						"Lorem ipsum dolor sit amet consectetur adipisicing elit. Aliquam labore beatae omnis quaerat excepturi atque veritatis repellat officia similique quo.",
					filterProps: ["Payment_Type", "Product"],
					filterValues: ["Visa", "Product3"]
				},
				{
					title: "All Mastercard Purchases",
					text:
						"Lorem ipsum dolor sit amet consectetur adipisicing elit. Eligendi corrupti quos sapiente, voluptatem perferendis rem aut quas veniam aliquid facilis nihil alias voluptas qui quae dicta totam! Amet, soluta accusamus.",
					filterProps: ["Payment_Type"],
					filterValues: ["Mastercard"]
				}
			]
		}),
		computed: {
			...mapState({
				config: state => state.config,
				appData: state => state.dataLoader.appData,
				storyData: state => state.story.storyData
			})
		},
		methods: {
			...mapActions({
				createStoryData: "story/createStoryData"
			}),
			prev() {
				if (this.index === 0) {
					this.index = this.slides.length - 1;
				} else {
					this.index--;
				}
			},
			next() {
				if (this.index >= this.slides.length - 1) {
					this.index = 0;
				} else {
					this.index++;
				}
			},
			updateStoryData(index) {
				console.log("update");
				this.slides[index].data = this.appData.geojson[this.config.Story.data];
				this.slides[index].id = "sales";
				this.createStoryData(this.slides[index]);
			}
		},

		watch: {
			index: {
				handler: function(val) {
					this.updateStoryData(val);
				},
				immediate: true
			}
		}
	};
</script>

<style lang="scss" scoped>
	#storyMap-container {
		height: calc(100vh - 118px);
		width: 100vw;
	}

	.prevIcon {
		position: absolute;
		top: calc(50vh - 70px);
		left: 10px;
		z-index: 10;
	}

	.nextIcon {
		position: absolute;
		top: calc(50vh - 70px);
		right: 10px;
		z-index: 10;
	}
</style>
